import { clone } from 'lodash';
import { Component, OnInit } from '@angular/core';
import { NAV_MENU } from './full-layout.navigation';
import { UserService } from 'modules/user/user.service';
import { User } from 'core/providers/user.service';
import { Access } from 'core/providers/access.service';
import { ProfileService, Name } from 'modules/user/profile/profile.service';

@Component({
  selector: 'app-root',
  templateUrl: './full-layout.component.html',
  providers: [UserService]
})
export class FullLayoutComponent implements OnInit {

  public disabled: boolean = false;
  public userName: string;
  public userEmail: string;
  public navMenu: Object[];

  public sidebarMinimized = true;
  private changes: MutationObserver;
  public element: HTMLElement = document.body;

  private logoutUrl = '/logout';

  constructor(
    private userService: UserService,
    private profileService: ProfileService,
    private access: Access,
    private user: User
  ) {

    this.changes = new MutationObserver((mutations) => {
      this.sidebarMinimized = document.body.classList.contains('sidebar-minimized')
    });

    this.changes.observe(<Element>this.element, {
      attributes: true
    });

    if (!this.user || !this.user.name) {

      this.__prepareProfile();

    } else {

      this.userName = this.user.name;
      this.userEmail = this.user.email_id;
    }
  }

  async ngOnInit() {

    const nav_menu = clone(NAV_MENU);
    this.navMenu = await this.__prepareNavigation(nav_menu);
  }

  private async __prepareNavigation(nav_menu) {

    let hasAccess, children;
    const accessMenu = [];

    for (const item of nav_menu) {

      if (item.children === undefined) {

        hasAccess = await this.access.hasAccess(item.access, item.url.substring(1));
        if (hasAccess) {

          delete item.access;
          accessMenu.push(item);
        }

      } else {

        children = await this.__prepareNavigation(item.children);
        if (children.length > 0) {

          item.children = children;
          accessMenu.push(item);
        }
      }
    }

    return accessMenu;
  }

  private async __prepareProfile() {

    this.profileService.getProfile('profile1').then(profile => {

      this.user.email_id = profile.email_id;
      this.user.name = Name.getFullName(profile.name);
      this.user.user_id = profile._id;

      this.userName = this.user.name;
      this.userEmail = this.user.email_id;
    });
  }

  logout() {

    this.userService.logout();
    window.location.replace(this.logoutUrl);
  }
}
