import { Injectable } from '@angular/core';
import { AppHttp } from 'core/services/app-http.service';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

class AccessibleEntity {
  module: string;
  entity: string;
  action?: string;
}

export class EntityList {

  entity: string;
  actions: Array<string>;
}

export class ModuleList {

  module: string;
  entities: Array<EntityList>;
}

@Injectable()
export class Access extends AppHttp {

  modules: Array<ModuleList>;

  private _access_url: string = '/profile/access';
  private global_routes = [
    'dashboard'
  ];

  constructor(protected http: HttpClient, private router: Router) {

    super(http);
    this.modules = new Array<ModuleList>();
  }

  private __loadAccess() {

    if (!this.isEmpty()) {
      return new Promise(resolve => resolve(true));
    }

    this.resetHeaders();

    return this.get(this._access_url)
      .then(response => {

        response = response.data;
        const access_data = new Array<ModuleList>();

        response.forEach(function (module) {

          const module_details = {
            module: module.module_code,
            entities: []
          }
          module.entities.forEach(function (entity) {

            module_details.entities.push({
              entity: entity.entity_code,
              actions: entity.actions
            });
          });
          access_data.push(module_details);
        });

        this.modules = access_data;
        return true;

      }).catch((error) => {

        if (error.status === 401) {

          this.resetLocalStorage();
          this.router.navigate(['/login']);

        } else if (error.status === 403) {

          this.router.navigate(['/forbidden']);

        } else {

          this.router.navigate(['/']);
        }

        return false;
      });
  }

  private __hasAccess(module: string, entity: string, action?: string) {

    let result = false;
    this.modules.forEach(function (module_details) {

      if (module_details.module === module) {

        module_details.entities.forEach(function (entity_details) {

          if (entity_details.entity === entity &&
            (action === undefined || entity_details.actions.indexOf(action) !== -1)) {
            result = true;
          }
        });
      }
    });
    return result;
  }

  protected resetLocalStorage() {

    localStorage.removeItem('sessionToken');
    this.resetHeaders();
  }

  public hasAccess(access: AccessibleEntity, path?: string, navigate?: boolean) {

    navigate = !!navigate;

    if (!this.isEmpty()) {

      if (path !== undefined && this.global_routes.indexOf(path) !== -1) {

        return true;

      } else if (access === undefined || access.module === undefined || access.entity === undefined) {

        if (navigate) {
          this.router.navigate(['forbidden']);
        }
        return false;
      }
    }

    return this.__loadAccess().then(response => {

      if (path !== undefined && this.global_routes.indexOf(path) !== -1) {

        return true;

      } else if (access === undefined || access.module === undefined || access.entity === undefined) {

        if (navigate) {
          this.router.navigate(['forbidden']);
        }
        return false;

      } else if (!this.__hasAccess(access.module, access.entity, access.action)) {

        if (navigate) {
          this.router.navigate(['forbidden']);
        }
        return false;
      }

      return true;
    });
  }

  public isEmpty() {

    return this.modules.length === 0;
  }

  public resetAccess() {

    this.modules = new Array<ModuleList>();
  }
};
