import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppHttp } from './app-http.service';
import { Router } from '@angular/router';

@Injectable()
export class Api extends AppHttp {

  constructor(protected http: HttpClient, protected router: Router) {
    super(http);
  }

  private __checkTokens() {

    if (this.headers.has('x-session-token')) {
      return;
    }

    this.resetHeaders();
  }

  protected resetLocalStorage() {

    localStorage.removeItem('sessionToken');
    this.resetHeaders();
  }

  protected resetSessionStorage() {

    sessionStorage.removeItem('restrict');
    sessionStorage.removeItem('url');
    this.resetHeaders();
  }

  protected handleApiError(error: any, url: string, data?: any) {

    if (error.status === 401) {

      this.resetLocalStorage();
      this.router.navigate(['/login']);

    } else if (error.status === 403) {
      this.router.navigate(['/forbidden']);
    }

    throw error;
  }

  get(url: string, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.get(url, query, config).catch((error) => {
      this.handleApiError(error, url, { query: query });
    });
  }

  put(url: string, data: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.put(url, data, query, config).catch((error) => {
      this.handleApiError(error, url, { query: query, data: data });
    });
  }

  patch(url: string, data?: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.patch(url, data, query, config).catch((error) => {
      this.handleApiError(error, url, { query: query, data: data });
    });
  }

  post(url: string, data: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.post(url, data, query, config).catch((error) => {
      this.handleApiError(error, url, { query: query, data: data });
    });
  }

  delete(url: string, data?: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    this.__checkTokens();
    return super.delete(url, data, query, config).catch((error) => {
      this.handleApiError(error, url, { query: query, data: data });
    });
  }
}
