import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

@Injectable()
export class OrganizationService extends Api {

  private _base_url: string = '/organizations';

  postOrg(organization: any) {

    return this.post(this._base_url, organization)
      .then(response => response);
  };

  getDetails(): any {

    return this.get(this._base_url)
      .then(response => response);
  }

  getTree(): any {

    return this.get(this._base_url + '/tree')
      .then(response => response);
  }
}
