import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute } from '@angular/router';
import { UserService } from 'modules/user/user.service';
import { User } from 'core/providers/user.service';

class Login {
  email_id: string = '';
  password: string = '';
}

@Component({
  selector: 'app-root',
  templateUrl: 'login.component.html',
  providers: [UserService]
})
export class LoginComponent implements OnInit {

  data: Login = new Login();

  returnUrl: string;

  error: string = '';

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private user: User
  ) {}

  ngOnInit() {

    // get return url from route parameters or default to '/'
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    this.error = '';
  }

  login() {

    if (!this.data.email_id || this.data.email_id.length < 3) {
      this.error = 'Please enter a valid username';
      return;
    } else if (!this.data.password || this.data.password.length < 5) {
      this.error = 'Please enter a valid password';
      return;
    }

    this.error = '';
    this.userService.login(this.data.email_id, this.data.password)
      .then(result => {

        this.user.user_id = result.user_id;
        this.user.name = result.name;
        this.user.email_id = result.email_id;
        this.user.change_password = !!result.change_password;

        if (result.mfa) {
          this.user.mfa = result.mfa;
        }

        if (result.change_password) {

          this.router.navigateByUrl('/user/change-password', { queryParams: { returnUrl: this.returnUrl } });

        } else if (result.mfa) {

          this.router.navigate(['/login/multi-factor'], { queryParams: { returnUrl: this.returnUrl } });

        } else {
          this.router.navigateByUrl(this.returnUrl);
        }

    }).catch(err => {
      this.error = err.error.message.text;
    });
  }
}
