import { Component, OnInit } from "@angular/core";
import { UserService } from "modules/user/user.service";
import { ActivatedRoute, Router } from "@angular/router";
import { User, UserMfa } from "core/providers/user.service";
import { Question, Answer } from "modules/user/profile/profile.service";
import { map, isEmpty } from "lodash";
import { MFA } from "config/constants/user";
import { ERROR } from "config/errors/user";
import { OtpService } from "core/services/otp.service";

@Component({
  selector: "app-root",
  templateUrl: "login-multi-factor.component.html",
  providers: [UserService, OtpService]
})
export class LoginMultiFactorComponent implements OnInit {

  mfa: UserMfa;
  returnUrl: string;
  error: string = "";

  questions: Array<Question>;
  answers: Array<Answer>;
  code: string;
  otpSent: boolean = false;
  otp: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private user: User,
    private otpService: OtpService
  ) { }

  ngOnInit() {

    // get return url from route parameters or default to "/"
    this.returnUrl = this.route.snapshot.queryParams["returnUrl"] || "/";
    this.error = "";

    this.mfa = this.user.mfa;

    if (!this.mfa) {
      this.router.navigateByUrl("/login", { queryParams: { returnUrl: this.returnUrl } });
    }

    if (this.mfa.mfa_type === MFA.QUESTION_ANSWER) {
      this.__prepareQuestions();
    } else if (this.mfa.mfa_type === MFA.EMAIL_ID) {

      this.otpService.sendOtp('login_email', { email_id: this.user.email_id })
        .then(response => {

          if (response.status === true) {
            this.otpSent = true;
          }
        });
    }
  }

  private __prepareQuestions() {

    this.questions = new Array();
    this.answers = new Array();
    let index;

    do {

      index = Math.floor(Math.random() * this.mfa.questions.length);
      const question_ids = map(this.questions, function (question) {
        return question.question_id;
      });

      if (question_ids.indexOf(this.mfa.questions[index].question_id) === -1) {

        this.questions.push(this.mfa.questions[index]);
        this.answers.push(new Answer());
        this.answers[this.answers.length - 1].question_id = this.mfa.questions[index].question_id;
      }

    } while (this.questions.length < 2);
  }

  verifyCode() {

    if (!this.code) {
      this.error = "Please enter code before submitting";
      return;
    }

    this.error = "";
    this.userService.validateMfa({ code: this.code })
      .then(result => {

        delete this.user.mfa;
        this.router.navigateByUrl(this.returnUrl);

      }).catch(err => {
        this.error = err.error.message.text;
      });
  }

  verifyAnswers() {

    let valid_answers = true;
    map(this.answers, function (answer) {
      if (isEmpty(answer.answer)) {
        valid_answers = false;
      }
    });

    if (!valid_answers) {
      this.error = "Please provide answer to both security questions before submitting";
      return;
    }

    this.error = "";
    this.userService.validateMfa({ answers: this.answers })
      .then(result => {

        delete this.user.mfa;
        this.router.navigateByUrl(this.returnUrl);

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.message.error_code === ERROR.INVALID_QUESTION_COUNT) {
          this.__prepareQuestions();
        }
      });
  }

  verifyOtp() {

    if (!this.otp) {
      this.error = "Please enter OTP before submitting";
      return;
    }

    this.error = "";
    this.userService.validateMfa({ otp: this.otp })
      .then(result => {

        delete this.user.mfa;
        this.router.navigateByUrl(this.returnUrl);

      }).catch(err => {
        this.error = err.error.message.text;
      });
  }
}
