import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { clone } from 'lodash';

import { environment } from '../../../environments/environment';

class Options {

  headers?: HttpHeaders;
  params?: HttpParams;
  body?: any;
}

@Injectable()
export class AppHttp {

  protected headers: HttpHeaders;

  constructor(protected http: HttpClient) {

    this.http = http;
    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Accept': 'application/json'
    });

    this.resetHeaders();
  }

  private __prepareOptions(query?: Map<string, any>, config?: Map<string, any>) {

    const options = new Options();
    options.headers = clone(this.headers);
    options.params = this.prepareParameters(query);

    if (config && config.has('headers')) {
      options.headers = config.get('headers');
    }

    return options;
  }

  protected resetHeaders() {

    this.headers = this.headers.delete('x-session-token');
    if (localStorage.getItem('sessionToken') !== null) {
      this.headers = this.headers.set('x-session-token', localStorage.getItem('sessionToken'));
    }
  }

  protected prepareParameters(query?: Map<string, any>): HttpParams {

    let params = new HttpParams();
    if (typeof query === 'undefined') {
      return params;
    }

    query.forEach(function (value, key) {
      params = params.set(key, value);
    });
    return params;
  }

  protected handleError(error: any, url: string, data?: any) {

    console.error('URL: ' + url);
    console.error('Data: ', data);
    console.error('Error: ', error);

    return Promise.reject(error);
  }

  get(url: string, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    const options = this.__prepareOptions(query, config);
    return this.http.get(environment.apiServer + url, options)
      .toPromise()
      .catch((error) => this.handleError(error, url, { query: query }));
  }

  put(url: string, data: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    const options = this.__prepareOptions(query, config);
    return this.http.put(environment.apiServer + url, data, options)
      .toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }

  post(url: string, data: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    const options = this.__prepareOptions(query, config);
    return this.http.post(environment.apiServer + url, data, options)
      .toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }

  patch(url: string, data?: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    const options = this.__prepareOptions(query, config);
    options.body = data;
    return this.http.request('patch', environment.apiServer + url, options)
      .toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }

  delete(url: string, data?: any, query?: Map<string, any>, config?: Map<string, any>): Promise<any> {

    const options = this.__prepareOptions(query, config);
    options.body = data;
    return this.http.request('delete', environment.apiServer + url, options)
      .toPromise()
      .catch((error) => this.handleError(error, url, { query: query, data: data }));
  }
}
