import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Google } from 'core/services/analytics/google.service';

@Component({
  // tslint:disable-next-line
  selector: 'body',
  template: '<router-outlet></router-outlet>',
  providers: [Google]
})
export class AppComponent implements OnInit {

  private _fixed_urls = [
    '/logout'
  ];

  constructor(private router: Router, private analytics: Google) { }

  ngOnInit() {

    this.router.events.subscribe(event => {

      if (event instanceof NavigationStart) {

        const restrict = sessionStorage.getItem('restrict'),
          url = sessionStorage.getItem('url');

        if (restrict === 'navigation' && url.length > 0 &&
          event.url !== url && this._fixed_urls.indexOf(event.url) === -1) {
          this.router.navigate([url]);
        }

      } else if (event instanceof NavigationEnd) {

        window.scrollTo(0, 0);
        this.analytics.pageview(event.urlAfterRedirects);
      }
    });
  }
}
