import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';

export class Name {

  first: string = '';
  middle: string;
  last: string = '';

  static getFullName(name) {

    let fullName = name.first;
    if (name.middle) {
      fullName += ' ' + name.middle;
    }
    fullName += ' ' + name.last;

    return fullName;
  }

  name(): string {

    return this.first + ' ' + this.last;
  }

  fullName(): string {

    return Name.getFullName(this);
  }
}

export class Address {

  address: string = '';
  locality: string = '';
  landmark: string = '';
  postcode: string = '';
  city: string = '';
  state: string = '';
  state_code: string = '';
  country: string = '';
  country_code: string = '';
}

export class EmergencyContact {

  name: string = '';
  relation: string = '';
  isd_code: string = '';
  contact_number: string = '';
}

export class ContactNumber {

  isd_code: string = '';
  contact_number: string = '';
}

export class SocialSecurity {

  type: string = '';
  number: string = '';
  file: string = '';
  verified: boolean = false;
}

export class Qualification {

  degree_id: string = '';
  institution_id: string = '';
  degree: string = '';
  level: string = '';
  specialization: string = '';
  institution: string = '';
  university: string = '';
  passing_year: number = 0;
  marks: number = 0;
}

export class Leave {

  leave_type: string;
  leaves: number;
}

export class Mfa {

  mfa_type: number;
  is_default: boolean;
  secret?: string;
  questions: Answer[];
}

export class Question {

  question_id: string;
  question: string;
}

export class Answer {

  question_id: string;
  answer: string;
}

export class DateOfBirth {

  day: number;
  month: number;
  year: number;
}

export class Profile {

  _id: string = '';
  name: Name = new Name();
  email_id: string = '';
  contact_number: string = '';
  isd_code: string = '';
  date_of_birth: DateOfBirth | string = new DateOfBirth();
  gender: string = '';
  permanent_address: Address = new Address();
  alternate_email_ids: string[] = new Array();
  alternate_contact_numbers: ContactNumber[] = new Array();
  marital_status: string = '';
  social_security: SocialSecurity[] = new Array();
  correspondence_address: Address = new Address();
  qualification: Qualification[] = new Array();
  emergency_contacts: EmergencyContact[] = new Array();
  leaves: Leave[] = new Array();
  mfa: Mfa[];
  recovery_codes: string[];
};

@Injectable()
export class ProfileService extends Api {

  private _base_url: string = '/profile';

  access() {

    return this.get(this._base_url + '/access')
      .then(response => response['data']);
  }

  getProfile(details?: string): Promise<Profile> {

    const query = new Map<string, string>();
    query.set('details', details);

    return this.get(this._base_url, query)
      .then(response => response as Profile);
  }

  updateProfile(profile: Object): Promise<Object> {

    return this.put(this._base_url, profile)
      .then(response => response);
  }

  changePassword(password: Object): Promise<Object> {

    return this.put(this._base_url + '/changepassword', password)
      .then(response => response);
  }
}
