import { Injectable } from '@angular/core';
import { Api } from 'core/services/api.service';
import { Access } from 'core/providers/access.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Injectable()
export class UserService extends Api {

  private _base_url: string = '/users';
  private _mfa_base_url: string = '/users/mfa';

  constructor(protected http: HttpClient, protected router: Router, private access: Access) {
    super(http, router);
  }

  login(email_id: string, password: string) {

    this.resetLocalStorage();
    this.resetSessionStorage();

    const data = {
      'email_id': email_id,
      'password': password
    };

    return this.post(this._base_url + '/login', data)
      .then(result => {

        result = result['data'];

        localStorage.setItem('sessionToken', result.session_token);
        this.resetHeaders();

        return result;
      }
    );
  }

  logout() {

    return this.post(this._base_url + '/logout', {})
      .then(response => {

        this.resetLocalStorage();
        this.resetSessionStorage();
        this.access.resetAccess();
      });
  }

  forgotPassword(email_id: string) {

    return this.get(this._base_url + '/forgotpassword?email_id=' + email_id)
      .then(response => response);
  }

  resetPassword(resetPasswordKey: string, password: string) {

    const data = {
      'reset_password_key': resetPasswordKey,
      'password': password
    };

    return this.put(this._base_url + '/resetpassword', data)
      .then(response => response['data']);
  }

  setPassword(setPasswordKey: string, password: string) {

    const data = {
      'set_password_key': setPasswordKey,
      'password': password
    };

    return this.post(this._base_url + '/setpassword', data)
      .then(response => response['data']);
  }

  initiateMfa(mfa_type: number) {

    const query = new Map<string, any>();
    query.set("mfa_type", mfa_type);

    return this.get(this._mfa_base_url, query)
      .then(response => response);
  }

  enableMfa(data: any) {

    return this.put(this._mfa_base_url, data)
      .then(response => response);
  }

  disableMfa(data: any) {

    return this.delete(this._mfa_base_url, data)
      .then(response => response);
  }

  validateMfa(data) {

    return this.post(this._mfa_base_url + '/validate', data)
      .then(response => response['data']);
  }
}
